.CreateTodoButton {
  background-color: #20dad8;
  box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
  border: none;
  cursor: pointer;
  font-size: 15px;
  position: fixed;
  top: 55px;
  right: 50px;
  font-weight: bold;
  color: #FAFAFA;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30px;
  width: 100px;
  z-index: 1;
  transform: rotate(0);
  transition: .3s ease;
  
}

.CreateTodoButton:hover {
  background-color: lightgreen;
  transform: scale(1.2);

}

@media (max-width: 560px){

  .CreateTodoButton{
    bottom: 24px;
    right: 150px;
  }
}